var md5 = require("md5");

String.prototype.toMd5 = function () {
  return md5(this);
};

Number.prototype.formatPrice = function () {
  return this.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,').replace(".00", "").replace(/,/g, ".");
};

String.prototype.formatPrice = function () {
  try {
    return parseInt(this).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,').replace(".00", "").replace(/,/g, ".");
  } catch (error) {}

  return this;
};

String.prototype.isEmail = function () {
  // var re = /^([a-zA-Z0-9]){4,}\@[a-zA-Z0-9][a-zA-Z0-9-]{1,61}[a-zA-Z0-9].[a-zA-Z]{2,}$/g;
  var re = /^([a-zA-Z0-9]+((\.|_)?[a-zA-Z0-9]+)*)+\@[a-zA-Z0-9][a-zA-Z0-9-]{1,61}([a-zA-Z0-9].[a-zA-Z]{2,})+$/g;
  return re.test(this.toLowerCase());
};

String.prototype.isNickname = function () {
  var re = /^([a-zA-Z0-9_]){6,30}$/g;
  return re.test(this.toLowerCase());
};

String.prototype.isFullName = function () {
  var re = /[0-9~!@#$%^&*()+\-_=\[\]{};':"\\|,.<>\/?]/;
  return !re.test(this);
};

String.prototype.isPhoneNumber = function () {
  // var re = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/g;
  // var re = /^([0]|([\+84]))[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{3,5}$/g;
  var re = /^(\+?84|0|\(\+?84\))[1-9]\d{8,9}$/g;
  return re.test(this);
};

String.prototype.isUrl = function () {
  try {
    var str = this.toLowerCase();
    var re = /^(?:(?:https?|http):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/;
    return re.test(str);
  } catch (error) {
    alert(error);
    return false;
  }
};

String.prototype.unsignText = function () {
  try {
    var str = this;
    str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
    str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
    str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
    str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
    str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
    str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
    str = str.replace(/đ/g, "d");
    str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
    str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
    str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
    str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
    str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
    str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
    str = str.replace(/Đ/g, "D");
    return str;
  } catch (error) {
    return this;
  }
};

String.prototype.removeWhiteSpace = function () {
  return this.replace(/ /g, "");
};

String.prototype.createUniqueText = function () {
  return this.unsignText().removeWhiteSpace();
};

String.prototype.toUpperCaseFullName = function () {
  var text = this;
  if (!text) return "";
  var a = text.split(" ").map(function (item) {
    var item2 = item + "";
    item2 = item2.charAt(0).toUpperCase() + item2.slice(1);
    return item2;
  });
  return a.join(" ");
};

module.exports = {
  guid: function guid() {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
    }

    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
  }
};

String.prototype.replaceAll = function (oldText, newText) {
  var text = this.toString();

  while (text.indexOf(oldText) != -1) {
    text = text.replace(oldText, newText);
  }

  return text;
};

String.prototype.toNumber = function () {
  try {
    return parseInt(this);
  } catch (error) {}

  return 0;
};